class EventBus<Event extends BaseEvent> {
  private handlers: Record<string, Set<EventHandler<any>> | undefined> = {}

  private getHandlers<Name extends Event['name']> (name: Name) {
    return this.handlers[name] ??= new Set
  }

  on<Name extends Event['name']> (name: Name, handler: EventHandler<EventByName<Event, Name>>) {
    this.getHandlers(name).add(handler)
  }

  off<Name extends Event['name']> (name: Name, handler: EventHandler<EventByName<Event, Name>>) {
    this.getHandlers(name).delete(handler)
  }

  emit<E extends Event> (event: E) {
    for (const handler of this.getHandlers(event.name)) handler(event)
  }
}

export default EventBus

export interface BaseEvent {
  name: string
}

export type EventByName<Event extends BaseEvent, Name extends Event['name']> = (
  Event extends { name: Name }
    ? Event
    : never
)

export type EventHandler<E extends BaseEvent> = (event: E) => void
