




























import { Vue, Component, Prop } from 'vue-property-decorator'
import TopBar from './TopBar.vue'
import Graph from './Graph.vue'
import Customizer from './Customizer.vue'
import Editor from './Editor.vue'
import Diagram from '@/diagram'

@Component({
  components: {
    TopBar,
    Graph,
    Customizer,
    Editor,
  },
})

export default class DiagramVue extends Vue {
  @Prop({ type: Diagram, required: true })
  readonly diagram!: Diagram

  editor = {
    show: false,
    object: null as unknown,
  }

  mounted () {
    this.$watch(
      () => this.diagram.activeDiagram.settings.activeFields,
      () => {
        this.diagram.eventBus.emit({ name: 'activeFieldsChanged' })
        this.diagram.eventBus.emit({ name: 'settingsChanged' })
      },
      { deep: true },
    )

    this.$watch(
      () => this.diagram.activeDiagram.settings.activeFilters,
      () => {
        this.diagram.eventBus.emit({ name: 'activeFiltersChanged' })
        this.diagram.eventBus.emit({ name: 'settingsChanged' })
      },
      { deep: true },
    )
  }

  showEditor (object: unknown) {
    this.editor = { show: true, object }
  }

  hideEditor () {
    this.editor = { show: false, object: null }
  }
}
