import Diagram, { Entity, EntityDefinition, EntityStyle, Relation, RelationDefinition, RelationStyle } from '@/diagram'

export default class DiagramData {
  constructor (diagram: Diagram, options?: {
    data?: DiagramDataDefinition
    changes?: Change[]
  }) {
    this.diagram = diagram

    if (options) {
      this.initialData = {
        entities: options.data?.entities?.map(item => new Entity(this.diagram, item)) ?? [],
        relations: options.data?.relations?.map(item => new Relation(this.diagram, item)) ?? [],
        changes: options.changes ?? [],
      }

      this.reset()
    }
  }

  diagram: Diagram

  initialData: {
    entities: Entity[]
    relations: Relation[]
    changes: Change[]
  } = {
    entities: [],
    relations: [],
    changes: [],
  }

  changes: CompiledChange[] = []
  undoneChanges: CompiledChange[] = []

  entitiesMap = new Map<string, Entity>()
  entities: Entity[] = []

  relationsMap = new Map<string, Relation>()
  relations: Relation[] = []

  get canUndo () {
    return this.changes.length > 0
  }

  get canRedo () {
    return this.undoneChanges.length > 0
  }

  reset () {
    this.changes = []
    this.undoneChanges = []

    this.entitiesMap.clear()
    this._addEntities(this.initialData.entities)

    this.relationsMap.clear()
    this._addRelations(this.initialData.relations)
  }

  add (options: DiagramDataDefinition) {
    this.commitChange({
      type: 'addition',
      entities: options.entities,
      relations: options.relations,
    })
  }

  addUnique (options: DiagramDataDefinition) {
    this.commitChange({
      type: 'addition',
      entities: options.entities?.filter(e => !this.entitiesMap.has(e.id)),
      relations: options.relations?.filter(r => {
        // TODO: I think we need an ID on Relations that isn't just autogenerated like now...
        // return !this.relationsMap.has(r.id)
        return true
      }),
    })
  }

  remove (options: {
    entityIds?: string[],
    relationIds?: string[],
  }) {
    this.commitChange({
      type: 'removal',
      entityIds: options.entityIds,
      relationIds: options.relationIds,
    })
  }

  commitChange (change: Change) {
    this.undoneChanges = []

    let apply: (() => void) | null = null
    let revert: (() => void) | null = null

    switch (change.type) {
      case 'addition': {
        apply = () => {
          const entities = change.entities?.map(e => {
            const after = new Entity(this.diagram, e)
            const before = this.entitiesMap.get(after.id)
            return { before, after }
          })

          const relations = change.relations?.map(r => {
            const after = new Relation(this.diagram, r)
            const before = this.relationsMap.get(after.id)
            return { before, after }
          })

          if (entities) this._addEntities(entities.map(e => e.after))
          if (relations) this._addRelations(relations.map(r => r.after))

          revert = () => {
            if (entities) {
              for (const entity of entities) {
                if (entity.before) this.entitiesMap.set(entity.before.id, entity.before)
                else this.entitiesMap.delete(entity.after.id)
              }

              this._updateEntitiesArray()
            }

            if (relations) {
              for (const relation of relations) {
                if (relation.before) this.relationsMap.set(relation.before.id, relation.before)
                else this.relationsMap.delete(relation.after.id)
              }

              this._updateRelationsArray()
            }
          }
        }
        break
      }

      case 'removal': {
        apply = () => {
          const entities: Entity[] = []
          const relations: Relation[] = []

          for (const id of change.entityIds ?? []) {
            const entity = this.entitiesMap.get(id)
            if (entity) entities.push(entity)
          }

          for (const id of change.relationIds ?? []) {
            const relation = this.relationsMap.get(id)
            if (relation) relations.push(relation)
          }

          if (entities.length) this._removeEntities(entities)
          if (relations.length) this._removeRelations(relations)

          revert = () => {
            if (entities.length) this._addEntities(entities)
            if (relations.length) this._addRelations(relations)
          }
        }
        break
      }

      case 'edit': {
        const entities = new Map<string, Entity>()
        const relations = new Map<string, Relation>()

        if (change.entities) for (const e of change.entities) {
          const entity = this.entitiesMap.get(e.id)
          if (entity) entities.set(entity.id, entity)
        }

        if (change.relations) for (const r of change.relations) {
          const relation = this.relationsMap.get(r.id)
          if (relation) relations.set(relation.id, relation)
        }

        apply = () => {
          const editedEntities: Entity[] = []
          const editedRelations: Relation[] = []

          if (change.entities) for (const edit of change.entities) {
            const entity = entities.get(edit.id)?.clone()
            if (entity) {
              entity.merge(edit)
              editedEntities.push(entity)
            }
          }

          if (change.relations) for (const edit of change.relations) {
            const relation = relations.get(edit.id)?.clone()
            if (relation) {
              relation.merge(edit)
              editedRelations.push(relation)
            }
          }

          if (editedEntities.length) this._addEntities(editedEntities)
          if (editedRelations.length) this._addRelations(editedRelations)
        }

        revert = () => {
          if (entities.size) this._addEntities([...entities.values()])
          if (relations.size) this._addRelations([...relations.values()])
        }
        break
      }
    }

    const compiledChange: CompiledChange = {
      change,
      apply: () => apply?.(),
      revert: () => {
        if (!revert) {
          console.warn('change.revert() was called before change was applied')
          return
        }

        revert()
        revert = null
      },
    }

    compiledChange.apply()
    this.changes.push(compiledChange)
  }

  undo () {
    const lastChange = this.changes.pop()
    if (!lastChange) return

    lastChange.revert()
    this.undoneChanges.push(lastChange)
  }

  redo () {
    const lastUndoneChange = this.undoneChanges.pop()
    if (!lastUndoneChange) return

    lastUndoneChange.apply()
    this.changes.push(lastUndoneChange)
  }

  private _addEntities (entities: Entity[]) {
    for (const entity of entities) this.entitiesMap.set(entity.id, entity.clone())
    this._updateEntitiesArray()
  }

  private _addRelations (relations: Relation[]) {
    for (const relation of relations) this.relationsMap.set(relation.id, relation.clone())
    this._updateRelationsArray()
  }

  private _removeEntities (entities: Entity[]) {
    for (const entity of entities) {
      this.entitiesMap.delete(entity.id)
    }
    this._updateEntitiesArray()
  }

  private _removeRelations (relations: Relation[]) {
    for (const relation of relations) {
      this.relationsMap.delete(relation.id)
    }
    this._updateRelationsArray()
  }

  private _updateEntitiesArray () {
    this.entities = [...this.entitiesMap.values()]
  }

  private _updateRelationsArray () {
    this.relations = [...this.relationsMap.values()]
  }
}

export interface DiagramDataDefinition {
  entities?: EntityDefinition[]
  relations?: RelationDefinition[]
}

export interface CompiledChange {
  change: Change
  apply: () => void
  revert: () => void
}

export type Change = (
  | AdditionChange
  | RemovalChange
  | EditChange
)

export interface AdditionChange {
  type: 'addition'
  entities?: EntityDefinition[]
  relations?: RelationDefinition[]
}

export interface RemovalChange {
  type: 'removal'
  entityIds?: string[]
  relationIds?: string[]
}

export interface EditChange {
  type: 'edit'
  entities?: ({
    id: string
    data?: Record<string, any>
    style?: EntityStyle
  })[]
  relations?: ({
    id: string
    data?: Record<string, any>
    style?: RelationStyle
  })[]
}
